@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@500&display=swap');

.home{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 10px;
    margin-top: 40px;
}
.left{
    margin-top: 20px;
    /* width: 60%; */
    border:1px solid chartreuse
}
.UpperName{
    display: flex;
    align-items: center;
    border:1px solid yellow;
    margin-top: 50px;
    margin-left: 30px;
}

.UpperName h1{
    font-weight: bold;
    font-size: 23px;
    font-family: 'Noto Sans', sans-serif;
}

.UpperName img{
    width: auto;
    height:40px;
}

.mainName h1{
    font-weight: bold;
    font-size: 50px;
    font-family: 'Noto Sans', sans-serif;
    text-align: left;
    margin-left: 30px;
}

.right{
    background-image: url("../../Images/Resume_photo.jpeg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    /* box-shadow: inset 0 0 0 9px rgba(219, 126, 34, 0.814); */
    box-shadow: inset 0 0 0 9px #078cbe9c;
    

    order: 1;
    justify-self: center;
    width: 300px;
    height: 300px;
    margin-top: 20px;
    animation: profile__animate 6s ease-in-out infinite 1s;
}

@keyframes profile__animate {
    0%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    50%{
        border-radius: 30% 60% 70% 60%/50% 60% 30% 60%;
    }
    100%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
}